.gallery{
    display: grid;
    max-width: calc(100vw - 48px);
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    grid-gap: 16px;
    margin-top: 0;
    margin-bottom: 0;
    padding: 0;
    list-style: none;
    margin-left: auto;
    margin-right: auto;
    padding: 40px;
}
.itemsImg{
    position: relative;
}
.itemImg{
    width: 100%;
    height: 500px;
    object-fit: cover;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.itemImg:hover {
    transform: scale(1.03);
    cursor: zoom-in;
  }
.posterTitle{
    text-align: center;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: bold;
    color: #000000;
}
.linkPoster{
    text-decoration: none;
}
.posterVote {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: #FF5722;
    color: #fff;
    padding: 8px;
    border-radius: 50%;
    font-size: 14px;
}